<template> <div class="message">
    <div class="message-header">
        <h5 v-text="theCardTitle"></h5> 
           </div> <div class="message-body"> 
               <!-- <p class="message-text">Brother</p>  -->
               <button @click="messageBrother" class="btn"></button> 
               <div v-if="fromBrother" class="alert" v-html="fromBrother">
               </div> 
            </div> 
    </div> 
</template>
<script> 
    import {eventBus} from '../main'
    export default 
    {
        //  props: ['messageSon'],
         data(){
             return{
                 theCardTitle:"Sister",
                 fromBrother:"",
             }
         },
         methods: {
              messageBrother()
                { 
                  eventBus.$emit('sisterSaid',"")
                } 
         },
         created(){
             eventBus.$on("brotherSaid",(message)=>{
                 this.fromBrother=message;
             })
         } 
    } 
</script>



